.header {
	padding-bottom: 25px;
}


.header .setting {
	display: flex;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
}

.header .setting input[type='range']:first-of-type {
	flex-basis: 40%;
}

.header .setting input, .header .setting i {
	cursor: pointer;
	color: #FFF;
}
.header .setting input, .header .setting > i:last-of-type {
	min-width: 18px;
}

.header .settingTow {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: space-evenly;
	-ms-flex-pack: space-evenly;
	justify-content: space-evenly;
	margin: 30px 0 20px;
}

.header .settingTow i {
	font-size: 1.2rem;
	cursor: pointer;
}