.audio {
    padding-bottom: 25px;
    height: 520px;
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-box-shadow: inset 0 0 13px #00456fdb;
    box-shadow: inset 0 0 13px #00456fdb;
    border-radius: 5px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
}

.audio .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 230%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 2;
}

.audio ul {
    list-style: none;
}

.audio ul li {
    z-index: 2;
    position: relative;
    padding-top: 20px;
    padding-bottom: 20px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    cursor: pointer;
    padding-left: 5px;
    opacity: 0.8;
}

.audio ul li.active {
    background-color: #015589c5;
    border-radius: 5px;
    opacity: 0.8;
    pointer-events: none;
    cursor: alias;
    color: #ffb4b4;
}

.audio ul li:hover {
    background-color: #015589c5;
    opacity: 0.8;
}

.audio ul li span {
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    font-size: 1.1rem;
    pointer-events: none;
}

.audio ul li span.audioName {
    flex-basis: 350px;
    text-align: left;
}

.audio ul li div {
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    font-size: 1rem;
    pointer-events: none;
    display: -webkit-box;
    display: -ms-flexbox;
    min-width: 208.22px;
    min-height: 21px;
    letter-spacing: 1px;
    word-spacing: -4px;
}

.audio::-webkit-scrollbar {
    width: 0.6rem;
    border-radius: 2rem;
}

.audio::-webkit-scrollbar-track {
    border-radius: 10px;
    background-color: #0155891e;
}

.audio::-webkit-scrollbar-thumb {
    background-color: #004b79;
    border-radius: 8px;
}