* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none;
  font-family: sans-serif, Arial, Helvetica, sans-serif;
  font-weight: normal;
  -webkit-transition: all 0.4s cubic-bezier(0.38, 1.15, 0.7, 1.1);
  transition: all 0.4s cubic-bezier(0.38, 1.15, 0.7, 1.1);
}

body {
  text-align: center;
  overflow: hidden;
  background-image: url(https://images.pexels.com/photos/3980364/pexels-photo-3980364.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: black;
  height: 100vh;
}

body .overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: -5;
}
body .overlayTest {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.829);
  z-index: 999999999;
}
body .overlayTest i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999999999;
  color: #fff;
}

.container {
  -webkit-box-shadow: 0 0 6px #fff;
  box-shadow: 0 0 6px #fff;
  color: #fff;
  margin: 30px auto;
  max-width: 800px;
  background-color: transparent;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 5px;
}
@media (max-width: 700px) {
  html {
    font-size: 95%;
  }
}

@media (max-width: 500px) {
  html {
    font-size: 90%;
  }
  .container {
    margin: 0;
    box-shadow: none;
  }
  .info {
    padding-top: 20px !important;
    padding-bottom: 30px !important;
  }
  .audio {
    padding-bottom: 15px !important;
    height: 350px !important;
  }
  .header .setting input[type="range"]:first-of-type {
    flex-basis: 55% !important;
    margin-right: 5% !important;
    margin-left: 5% !important;
    margin-bottom: 20px !important;
  }
  .header .setting input {
    margin-right: 10% !important;
  }
  .header .setting > i:last-of-type {
    margin-left: 10% !important;
  }
  .header .setting i.download {
    transform: translateX(-50px);
  }
  footer {
    padding-top: 15px !important;
    padding-bottom: 15px !important;
  }
}

